<template>
  <section class="w100 position-relative" style="margin: -3vh 0;">
    <img
      src="../../../../assets/imgs/dashboard/big-data/earth-radar.png"
      alt="雷达图"
      class="img-suite"
    />
    <div
      class="position-absolute level-number"
      :class="numberLever[index]"
      v-for="(item, index) in needAlarmTypeData"
      :key="index"
    >
      <span class="position-relative" :class="circleLevel[index]"></span>
      <span class="mr5">{{ item.shortName }}</span>
      <span>{{ item.value }}</span>
    </div>
  </section>
  <section class="w100 flex-column just-between" style="height: 5.626vw;">
    <alarm-type-list :alarmTypeData="sortedData" />
  </section>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import AlarmTypeList from "@/components/dashboard/big-data/alarmTypeDistribution/alarmTypeList.vue";

export default defineComponent({
  name: "alarmTypeDistribution",
  components: { AlarmTypeList },
  props: {
    alarmTypeData: {
      type: Array as PropType<
        { shortName: string; longName: string; value: number }[]
      >,
      default: () => [
        { shortName: "安监", longName: "安全监测告警", value: 0 },
        { shortName: "井下运输", longName: "井下运输告警", value: 0 },
        { shortName: "井下人员", longName: "井下人员告警", value: 0 },
        { shortName: "供电", longName: "供电监控告警", value: 0 },
        { shortName: "工业视频", longName: "工业视频告警", value: 0 },
        { shortName: "矿压", longName: "矿压矿震告警", value: 0 },
        { shortName: "设备", longName: "重大设备告警", value: 0 },
        { shortName: "水文地质", longName: "水文地质告警", value: 0 },
        { shortName: "边坡", longName: "边坡监测告警", value: 0 }
      ]
    }
  },
  setup(props) {
    const numberLever = [
      "one-level-number",
      "two-level-number",
      "three-level-number",
      "four-level-number"
    ];
    const circleLevel = [
      "one-circle",
      "two-circle",
      "three-circle",
      "four-circle"
    ];
    function sortRule(
      num1: { shortName: string; longName: string; value: number },
      num2: { shortName: string; longName: string; value: number }
    ) {
      return num2.value - num1.value;
    }
    const needAlarmTypeData = props.alarmTypeData.sort(sortRule).slice(0, 4);
    const sortedData = props.alarmTypeData.sort(sortRule);
    return { numberLever, circleLevel, needAlarmTypeData, sortedData };
  }
});
</script>

