import mapStyleJson from "@/assets/json/custom_map_config.json";
interface IPoint {
  lat: number;
  lng: number;
  name: string;
  level?: number;
}

class BMap {
  map: any;
  BMapGL: any;
  zoom = [7, 10, 13];
  curZoomIndex = 1;

  constructor() {
    const BaiMap = (window as any).BMapGL;
    this.BMapGL = BaiMap;
    if (BaiMap) {
      this.map = new this.BMapGL.Map("bk-map");
      const point = new this.BMapGL.Point(
        110.50470423456488,
        38.84169661760584
      ); // 创建点坐标
      this.map.setMapStyleV2({ styleJson: mapStyleJson });
      this.map.centerAndZoom(point, this.zoom[this.curZoomIndex]);
      // const zoomCtrl = new this.BMapGL.ZoomControl();
      // this.map.addControl(zoomCtrl);
      this.map.enableContinuousZoom();
      this.map.enableScrollWheelZoom();
      this.map.onclick = function(e: any) {
        console.log(e);
      };
    } else {
      setTimeout(() => {
        return new BMap();
      }, 1000);
    }
  }

  setZoom(size: number) {
    const cur = this.curZoomIndex + size;
    if (cur > 2 || cur < 0) {
      return;
    }
    this.curZoomIndex = cur;
    this.map.centerAndZoom(this.map.getCenter(), this.zoom[cur]);
  }

  addPoint(pointInfo: IPoint) {
    const imgs = ["first-warn-map", "expired-documents", "offline-colliery"][
      pointInfo.level ?? 2
    ];
    const size = [
      [235, 243],
      [88, 78],
      [78, 78]
    ][pointInfo.level ?? 2];
    const myIcon = new this.BMapGL.Icon(
      require(`../../assets/svg/dashboard/big-data/map/${imgs}.svg`),
      new this.BMapGL.Size(size[0], size[1]),
      {}
    );
    const point = new this.BMapGL.Point(pointInfo.lat, pointInfo.lng);
    const marker = new this.BMapGL.Marker(point, { icon: myIcon });
    this.map.addOverlay(marker);
    const label = new this.BMapGL.Label(pointInfo.name, {
      // 创建文本标注
      position: point, // 设置标注的地理位置
      offset: new this.BMapGL.Size(20, -10) // 设置标注的偏移量
    });
    label.setStyle({
      backgroundColor: "transparent",
      color: "#06F398",
      border: "unset"
    });
    this.map.addOverlay(label);
    if ((pointInfo.level ?? 2) === 0) {
      const opts = {
        width: 0, // 信息窗口宽度
        height: 0, // 信息窗口高度
        title: "海湾煤矿", // 信息窗口标题,
        offset: new this.BMapGL.Size(200, 240),
        enableCloseOnClick: false
      };
      const infoWindow = new this.BMapGL.InfoWindow(
        `
        <div class="flex-column">
          <span>煤矿评分: 87</span>
          <span>煤矿状态：生产中</span>
          <span>证件状态: 三个月内到期</span>
          <span>今日报警: 1234</span>
        </div>
      `,
        opts
      ); // 创建信息窗口对象
      // marker.addEventListener("click", () => {
      this.map.openInfoWindow(infoWindow, point);
      // });
    }
  }
}

export default BMap;
