
import { defineComponent, onMounted, ref } from "vue";
import AlarmLevel from "@/components/dashboard/big-data/alarmLevel.vue";
import RiskEvaluation from "@/components/dashboard/big-data/riskEvaluation/riskEvaluation.vue";
import EnvInfo from "@/components/dashboard/common/env-info.vue";
import RiskCharts from "@/components/dashboard/big-data/riskEvaluation/riskCharts.vue";
import AlarmTypeDistribution from "@/components/dashboard/big-data/alarmTypeDistribution/alarmTypeDistribution.vue";
import { communicate } from "@/utils";
import { createFirstWarn } from "@/components/dashboard/common/first-warn/first-warn.logic";
import BMap from "@/utils/modules/BMap";
import { createWarn } from "@/components/dashboard/common/warn-message/create-warn";
import {
  getAlarmData,
  getWarnMessageList,
  getCheckingData,
  getRiskData,
  getRiskChartData,
  getWellData,
  getAlarmTypeData
} from "./big-data.simulate";
import WellStatisticalTop from "@/components/dashboard/big-data/wellStatistical/wellStatisticalTop.vue";
import WellStatisticalCenter from "@/components/dashboard/big-data/wellStatistical/wellStatisticalCenter.vue";
import WellStatisticalBottom from "@/components/dashboard/big-data/wellStatistical/wellStatisticalBottom.vue";
import DataSelect from "@/components/dashboard/common/data-select.vue";
import ScrollList from "@/components/dashboard/big-data/scroll-list.vue";
import WarnMessage from "@/components/dashboard/big-data/warnMessage.vue";
import CheckTable from "@/components/dashboard/big-data/checkTable.vue";
import AlarmInfo from "@/components/dashboard/big-data/alarmInfo.vue";
import FullChange from "@/components/dashboard/common/full-change.vue";

export default defineComponent({
  name: "big-data",
  components: {
    FullChange,
    AlarmInfo,
    WellStatisticalBottom,
    WellStatisticalCenter,
    WellStatisticalTop,
    ScrollList,
    DataSelect,
    AlarmTypeDistribution,
    RiskCharts,
    EnvInfo,
    AlarmLevel,
    RiskEvaluation,
    WarnMessage,
    CheckTable
  },
  setup() {
    const map = ref();
    const alarmRef = ref();
    function getMapScript() {
      map.value = new BMap();
      map.value.addPoint({
        lat: 111.00470423456488,
        lng: 39.04169661760584,
        name: "一个煤矿"
      });
      map.value.addPoint({
        lat: 110.21423456488,
        lng: 39.14169761760584,
        name: "一个煤矿2",
        level: 1
      });
      map.value.addPoint({
        lat: 110.41423456488,
        lng: 38.74169761760584,
        name: "一个煤矿2",
        level: 0
      });
    }

    onMounted(() => {
      getMapScript();
    });

    return {
      alarmData: getAlarmData(),
      warnMessageList: getWarnMessageList(),
      areaChange: (msg: string) => {
        communicate.sendMessage(msg);
      },
      checkingData: getCheckingData(),
      riskData: getRiskData(),
      riskChartData: getRiskChartData(),
      wellData: getWellData(),
      alarmTypeData: getAlarmTypeData(),
      setZoom(size: number) {
        map.value?.setZoom(size);
      },
      alarmRef,
      openAlarm() {
        alarmRef.value?.openModal();
      },
      tt(index: number) {
        switch (true) {
          case index === 0: {
            return createFirstWarn({
              level: 1,
              dur: 60,
              info: {
                name: "海湾煤矿",
                reason: "甲烷超标 2.6CH4 15分钟"
              }
            });
          }
          case index === 1: {
            return createWarn({
              info: {
                name: "海湾煤矿",
                reason: "甲烷超标 2.6CH4 15分钟"
              },
              par: ".map-top"
            });
          }
          case index === 2: {
            return createFirstWarn({
              level: 2,
              dur: 60
            });
          }
        }
      }
    };
  }
});
