<template>
  <ul ref="all" class="w100 second-warn flex-row align-center just-between">
    <li ref="content" class="flex-row flex-1 align-center">
      <img
        class="second-warn__img"
        src="../../../../assets/imgs/dashboard/common/warn.png"
        alt=""
      />
      <div class="second-warn__scroll">
        <span>{{ info.name }}</span>
        <span>「二级报警」</span>
        <slot item="11" />
        <span>{{ info.reason }}</span>
      </div>
    </li>
    <li class="second-warn__close" @click="close">+</li>
  </ul>
</template>

<script lang="ts">
import { defineComponent, onMounted, onUnmounted, ref } from "vue";
import animate from "animejs";

export default defineComponent({
  name: "second-warn",
  props: {
    info: {
      type: Object,
      default: () => ({
        name: "海外煤矿",
        reason: "甲烷超标 2.6CH4  8分钟"
      })
    },
    close: {
      type: Function,
      default: () => {}
    },
    dur: {
      type: Number,
      default: 60
    }
  },
  setup(props) {
    const content = ref<HTMLDivElement>();
    const all = ref<HTMLDivElement>();
    const timeOut = ref<number>();
    // const scroll = reactive({
    //   state: false,
    //   width: 0
    // });
    onMounted(() => {
      const cc = content.value?.getClientRects()[0];
      if (cc?.width) {
        const contentWidth = cc.width;
        const allWidth = all.value?.getClientRects()[0].width;
        if (contentWidth && allWidth && contentWidth > allWidth) {
          // scroll.state = true;
          // scroll.width = contentWidth - allWidth;
          animate({
            targets: ".second-warn__scroll",
            translateX: -contentWidth,
            loop: true,
            easing: "linear",
            endDelay: 0,
            duration: 10000
          });
        }
      }
      timeOut.value = setTimeout(() => {
        props.close();
      }, props.dur * 1000);
    });
    onUnmounted(() => {
      clearTimeout(timeOut.value);
    });
    return { content, all };
  }
});
</script>

<style scoped lang="less">
.second-warn {
  position: relative;
  padding: 0.5vw;
  margin-top: 1vw;
  overflow: hidden;
  font-size: 2.8rem;
  font-weight: 300;
  color: #faab1b;
  white-space: nowrap;
  background: #faab1b20;
  border-top: 1px solid #faab1b;
  border-bottom: 1px solid #faab1b;

  &__img {
    margin-right: 1vw;
  }

  &__close {
    position: absolute;
    right: 0;
    font-size: 5rem;
    cursor: pointer;
    transform: rotate(45deg);
    transform-origin: center;
  }
}
</style>
