
import { computed, defineComponent, onMounted, PropType, ref } from "vue";
const __default__ = defineComponent({
  name: "scroll-list",
  props: {
    data: {
      type: Array as PropType<IObj[]>,
      default: () => []
    },
    initialHeight: {
      type: String,
      default: ""
    },
    length: {
      type: Number,
      default: 5
    },
    dur: {
      type: Number,
      default: 5
    }
  },
  setup(props) {
    const curIndex = ref(0);
    const newMessage = ref<IObj[]>([[]]);
    const timerID = ref<number>();
    // 计算当前页和数据长度比较
    const isLast = computed(
      () => curIndex.value === newMessage.value.length - 1
    );
    const isFirst = computed(
      () => curIndex.value < 1 && curIndex.value < newMessage.value.length
    );

    function splitData() {
      // 将数据切分
      props.data.forEach((i: IObj) => {
        const lastArr = newMessage.value[newMessage.value.length - 1];
        if (lastArr.length < props.length) {
          lastArr.push(i);
        } else {
          newMessage.value.push([i]);
        }
      });
    }

    // 定时器装置
    const openTimer = () => {
      timerID.value = setInterval(() => {
        if (newMessage.value.length - 1 !== curIndex.value) {
          curIndex.value += 1;
        } else {
          curIndex.value = 0;
        }
      }, props.dur * 1000);
    };

    const removeTimer = () => {
      clearInterval(timerID.value);
    };

    onMounted(async () => {
      splitData();
      openTimer();
    });

    return {
      newMessage,
      curIndex,
      gotoPage() {
        !isLast.value && (curIndex.value += 1);
      },
      downPage() {
        !isFirst.value && (curIndex.value -= 1);
      },
      isLast,
      isFirst,
      icon: "assets/svg/dashboard/big-data/warn-message/",
      translate: `calc(-${100 * props.length}% - ${0.8 * props.length}rem)`,
      removeTimer,
      openTimer
    };
  }
});

