
import { defineComponent, reactive, toRefs } from "vue";
import moment from "moment";
export default defineComponent({
  name: "env-info",
  setup() {
    const data = reactive({
      date: "",
      time: "",
      temperature: "19",
      pm: "23"
    });
    setInterval(() => {
      data.time = moment().format("HH:mm:ss");
      data.date = moment().format("YYYY.M.D");
    });
    return {
      ...toRefs(data)
    };
  }
});
