<template>
  <div>
    <section class="flex-column">
      <span class="well-number">
        <NumAnimate :round="1">{{ actualWellNumber }}</NumAnimate>
      </span>
      <span class="well-text">实际下井</span>
    </section>
    <section class="flex-1 position-relative flex-column just-center">
      <img
        src="../../../../assets/svg/dashboard/big-data/well-statistical/top-back.svg"
        alt="下井人数比例"
        class="well-photo"
      />
      <span class="position-absolute well-percent">
        <NumAnimate :round="1">{{ progressData }}</NumAnimate>
      </span>
      <div class="position-absolute well-progress-bar flex-column just-center">
        <div class="progress-bar-back" :style="{ width: progressData }" />
      </div>
    </section>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";
import NumAnimate from "@/components/dashboard/common/num-animate.vue";

export default defineComponent({
  name: "wellStatisticalTop",
  components: { NumAnimate },
  props: {
    actualWellNumber: {
      type: Number,
      default: 0
    }
  },
  setup(props) {
    const progressData = ref<string>();
    onMounted(() => {
      progressData.value =
        Math.round((props.actualWellNumber / 4000) * 10000) / 100.0 + "%";
    });
    return {
      progressData
    };
  }
});
</script>

