
import { defineComponent, onMounted, ref } from "vue";

export default defineComponent({
  name: "wellStatisticalCenter",
  components: {},
  props: {
    wellData: {
      type: Object,
      default: () => ({
        actualWellNumber: 0,
        withoutLicenseNumber: 0,
        withoutLeaderNumber: 0,
        overTimeNumber: 0,
        overPersonNumber: 0,
        seekHelpNumber: 0
      })
    }
  },
  emits: {},
  setup(props) {
    const leftProgressData = ref<string>();
    const rightProgressData = ref<string>();
    onMounted(() => {
      const sum =
        props.wellData.withoutLicenseNumber +
        props.wellData.withoutLeaderNumber;
      leftProgressData.value =
        Math.round((props.wellData.withoutLicenseNumber / sum) * 10000) /
          (100 * 2) +
        "%";
      rightProgressData.value =
        Math.round((props.wellData.withoutLeaderNumber / sum) * 10000) /
          (100 * 2) +
        "%";
    });
    return {
      leftProgressData,
      rightProgressData
    };
  }
});
