<template>
  <div>
    <section
      class="flex-1 flex-column just-between align-center"
      v-for="(item, index) in chartData"
      :key="index"
    >
      <div class="position-relative" style="margin: auto;">
        <img
          src="../../../../assets/svg/dashboard/big-data/well-statistical/bottom-pie-back.svg"
          alt="饼底图"
          class="well-pie"
        />
        <span
          class="position-absolute well-pie-number"
          :style="{ color: chartColor[index] }"
        >
          <NumAnimate :round="1">{{ item[0] }}</NumAnimate>
        </span>
        <pie-charts
          class="position-absolute chart-pie"
          :chartColor="chartColor[index]"
          :chartData="item"
        />
      </div>
      <span class="well-pie-text" :style="{ color: chartColor[index] }">
        {{ chartText[index] }}
      </span>
    </section>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import PieCharts from "@/components/dashboard/big-data/wellStatistical/pieCharts.vue";
import NumAnimate from "@/components/dashboard/common/num-animate.vue";

export default defineComponent({
  name: "wellStatisticalBottom",
  components: { PieCharts, NumAnimate },
  props: {
    wellData: {
      type: Object,
      default: () => ({
        actualWellNumber: 0,
        withoutLicenseNumber: 0,
        withoutLeaderNumber: 0,
        overTimeNumber: 0,
        overPersonNumber: 0,
        seekHelpNumber: 0
      })
    }
  },
  setup(props) {
    const chartColor = ["#06F398", "#FACB1B", "#E72417"];
    const chartText = ["超时", "超员", "人员报警"];

    function getChartData(num: number, sum: number) {
      const start = Math.round((num / sum) * 100);
      const end = 100 - start;
      return [start, end];
    }

    const sum =
      props.wellData.overTimeNumber +
      props.wellData.overPersonNumber +
      props.wellData.seekHelpNumber;
    const arr1 = getChartData(props.wellData.overTimeNumber, sum);
    const arr2 = getChartData(props.wellData.overPersonNumber, sum);
    const arr3 = getChartData(props.wellData.seekHelpNumber, sum);
    const chartData = [arr1, arr2, arr3];

    return { chartColor, chartText, chartData };
  }
});
</script>

