<template>
  <ul class="mb0">
    <li class="flex-column just-center no-wrap align-end">
      <span class="number">{{ wellData.withoutLicenseNumber }}</span>
      <span class="text">无证下井</span>
    </li>
    <li class="position-relative h100">
      <img
        src="../../../../assets/svg/dashboard/big-data/well-statistical/center-back.svg"
        alt="下井人数比例"
        class="well-person-image"
      />
      <div class="left-progress-bar" :style="{ width: leftProgressData }" />
      <div class="right-progress-bar" :style="{ width: rightProgressData }" />
    </li>
    <li class="flex-column just-center no-wrap">
      <span class="number">{{ wellData.withoutLeaderNumber }}</span>
      <span class="text">无领导下井</span>
    </li>
  </ul>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";

export default defineComponent({
  name: "wellStatisticalCenter",
  components: {},
  props: {
    wellData: {
      type: Object,
      default: () => ({
        actualWellNumber: 0,
        withoutLicenseNumber: 0,
        withoutLeaderNumber: 0,
        overTimeNumber: 0,
        overPersonNumber: 0,
        seekHelpNumber: 0
      })
    }
  },
  emits: {},
  setup(props) {
    const leftProgressData = ref<string>();
    const rightProgressData = ref<string>();
    onMounted(() => {
      const sum =
        props.wellData.withoutLicenseNumber +
        props.wellData.withoutLeaderNumber;
      leftProgressData.value =
        Math.round((props.wellData.withoutLicenseNumber / sum) * 10000) /
          (100 * 2) +
        "%";
      rightProgressData.value =
        Math.round((props.wellData.withoutLeaderNumber / sum) * 10000) /
          (100 * 2) +
        "%";
    });
    return {
      leftProgressData,
      rightProgressData
    };
  }
});
</script>

<style scoped lang="less">
.well-person-image {
  max-width: 100%;
  max-height: 100%;
}

.left-progress-bar,
.right-progress-bar {
  position: absolute;
  bottom: 6%;
  width: 0;
  height: 0.4rem;
  background: #62ffcb;
  border-radius: 1rem;
  transition: width 1s;
}

.left-progress-bar {
  right: 52%;
}

.right-progress-bar {
  left: 52%;
}

