import { VNode } from "@vue/runtime-core";
import { createVNode, isVNode, render } from "vue";
import FirstWarn from "./first-warn.vue";

interface IOption {
  children?: VNode;
  level: 1 | 2;
  dur?: number;
  info?: {
    name: string;
    reason: string;
  };
}

let seed = 1;
let vm: VNode;

export function createFirstWarn(opts: IOption) {
  const container = document.createElement("div");
  const id = "message_" + seed++;
  container.className = `position-absolute container_${id}`;
  container.style.zIndex = "100";
  container.style.left = "0";
  container.style.right = "0";
  container.style.top = "0";
  container.style.bottom = "0";

  vm = createVNode(
    FirstWarn,
    { ...(opts as IObj), close: () => document.body.removeChild(container) },
    isVNode(opts.children)
      ? {
          default: () => opts.children
        }
      : null
  );

  render(vm, container);
  if (!opts.info) {
    container.onclick = () => {
      document.body.removeChild(container);
    };
  }

  setTimeout(() => {
    document.body.removeChild(container);
  }, (opts.dur || 60) * 1000);
  document.body.appendChild(container);
  return {
    close() {
      document.body.removeChild(container);
    }
  };
}
