
import { defineComponent, nextTick, onMounted, Ref, ref } from "vue";
import useEcharts from "@/hooks/common/useEchart";

export default defineComponent({
  name: "pieCharts",
  props: {
    chartColor: {
      type: String,
      default: "06F398"
    },
    chartData: {
      type: Array,
      default: () => [0, 0]
    }
  },
  setup(props) {
    const chartRef = ref<HTMLDivElement | null>(null);
    const { setOptions } = useEcharts(chartRef as Ref<HTMLDivElement>);
    onMounted(() => {
      setOptions({
        series: [
          {
            type: "pie",
            center: ["50%", "50%"],
            radius: ["56%", "86%"],
            labelLine: {
              show: false
            },
            data: [
              {
                value: props.chartData[0],
                itemStyle: { color: props.chartColor }
              },
              {
                value: props.chartData[1],
                itemStyle: { color: "transparent" }
              }
            ]
          }
        ]
      });
    });
    return {
      chartRef
    };
  }
});
