<template>
  <div class="w100 h100 position-relative">
    <div id="bk-map" class="bk-map h100 w100" />
    <ul class="map-left flex-column just-between position-absolute mb0">
      <li>
        <h1 class="title_main">神木能源安监平台</h1>
        <h3 class="title_min">SM DATA MONITORING</h3>
      </li>
      <li>
        <h3 class="title-flag">下井人员统计</h3>
        <div class="flex-column just-between" style="height: 20vw;">
          <well-statistical-top
            class="flex-column just-between"
            :actualWellNumber="wellData.actualWellNumber"
          />
          <well-statistical-center
            class="flex-row just-between w100"
            :wellData="wellData"
          />
          <well-statistical-bottom
            class="flex-row just-between"
            :wellData="wellData"
          />
        </div>
      </li>
      <li>
        <h3 class="title-flag">报警类型分布</h3>
        <div class="flex-column" style="height: 18.604vw;">
          <alarm-type-distribution :alarmTypeData="alarmTypeData" />
        </div>
      </li>
    </ul>
    <section class="map-top position-absolute">
      <div class="flex-row">
        <DataSelect class="area-select" @dataChange="areaChange" />
        <AlarmLevel
          v-for="(i, idx) of alarmData"
          class="alarm-level"
          :key="idx"
          :level="idx + 1"
          :alarm-num="i"
          @click="tt(idx)"
        />
      </div>
    </section>
    <EnvInfo class="env-info" />
    <section class="map-right position-absolute flex-column">
      <section class="flex-column just-between flex-1">
        <h3 class="title-flag-r">风险评估</h3>
        <div class="flex-row" style="height: 10.313vw;">
          <risk-charts
            class="h100"
            style="width: 8.177vw;"
            :riskChartData="riskChartData"
          />
          <risk-evaluation
            class="h100"
            style="width: 10.938vw;"
            :riskData="riskData"
          />
        </div>
        <h3 class="title-flag-r">报警消息</h3>
        <ScrollList :data="warnMessageList" style="height: 25vh;">
          <template v-slot="{ item }">
            <WarnMessage @click="openAlarm" :item="item" />
          </template>
        </ScrollList>
        <h3 class="title-flag-r">校验平台</h3>
        <ScrollList :data="checkingData" :length="3" style="height: 17vh;">
          <template v-slot="{ item }">
            <CheckTable :item="item" />
          </template>
        </ScrollList>
      </section>
    </section>
    <section class="map-control flex-column position-absolute">
      <img
        class="cur-pointer"
        src="../../../assets/svg/dashboard/big-data/map/plus.svg"
        alt=""
        @click="() => setZoom(1)"
      />
      <img
        class="cur-pointer"
        src="../../../assets/svg/dashboard/big-data/map/subtract.svg"
        alt=""
        @click="() => setZoom(-1)"
      />
    </section>
    <AlarmInfo ref="alarmRef" />
    <FullChange />
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";
import AlarmLevel from "@/components/dashboard/big-data/alarmLevel.vue";
import RiskEvaluation from "@/components/dashboard/big-data/riskEvaluation/riskEvaluation.vue";
import EnvInfo from "@/components/dashboard/common/env-info.vue";
import RiskCharts from "@/components/dashboard/big-data/riskEvaluation/riskCharts.vue";
import AlarmTypeDistribution from "@/components/dashboard/big-data/alarmTypeDistribution/alarmTypeDistribution.vue";
import { communicate } from "@/utils";
import { createFirstWarn } from "@/components/dashboard/common/first-warn/first-warn.logic";
import BMap from "@/utils/modules/BMap";
import { createWarn } from "@/components/dashboard/common/warn-message/create-warn";
import {
  getAlarmData,
  getWarnMessageList,
  getCheckingData,
  getRiskData,
  getRiskChartData,
  getWellData,
  getAlarmTypeData
} from "./big-data.simulate";
import WellStatisticalTop from "@/components/dashboard/big-data/wellStatistical/wellStatisticalTop.vue";
import WellStatisticalCenter from "@/components/dashboard/big-data/wellStatistical/wellStatisticalCenter.vue";
import WellStatisticalBottom from "@/components/dashboard/big-data/wellStatistical/wellStatisticalBottom.vue";
import DataSelect from "@/components/dashboard/common/data-select.vue";
import ScrollList from "@/components/dashboard/big-data/scroll-list.vue";
import WarnMessage from "@/components/dashboard/big-data/warnMessage.vue";
import CheckTable from "@/components/dashboard/big-data/checkTable.vue";
import AlarmInfo from "@/components/dashboard/big-data/alarmInfo.vue";
import FullChange from "@/components/dashboard/common/full-change.vue";

export default defineComponent({
  name: "big-data",
  components: {
    FullChange,
    AlarmInfo,
    WellStatisticalBottom,
    WellStatisticalCenter,
    WellStatisticalTop,
    ScrollList,
    DataSelect,
    AlarmTypeDistribution,
    RiskCharts,
    EnvInfo,
    AlarmLevel,
    RiskEvaluation,
    WarnMessage,
    CheckTable
  },
  setup() {
    const map = ref();
    const alarmRef = ref();
    function getMapScript() {
      map.value = new BMap();
      map.value.addPoint({
        lat: 111.00470423456488,
        lng: 39.04169661760584,
        name: "一个煤矿"
      });
      map.value.addPoint({
        lat: 110.21423456488,
        lng: 39.14169761760584,
        name: "一个煤矿2",
        level: 1
      });
      map.value.addPoint({
        lat: 110.41423456488,
        lng: 38.74169761760584,
        name: "一个煤矿2",
        level: 0
      });
    }

    onMounted(() => {
      getMapScript();
    });

    return {
      alarmData: getAlarmData(),
      warnMessageList: getWarnMessageList(),
      areaChange: (msg: string) => {
        communicate.sendMessage(msg);
      },
      checkingData: getCheckingData(),
      riskData: getRiskData(),
      riskChartData: getRiskChartData(),
      wellData: getWellData(),
      alarmTypeData: getAlarmTypeData(),
      setZoom(size: number) {
        map.value?.setZoom(size);
      },
      alarmRef,
      openAlarm() {
        alarmRef.value?.openModal();
      },
      tt(index: number) {
        switch (true) {
          case index === 0: {
            return createFirstWarn({
              level: 1,
              dur: 60,
              info: {
                name: "海湾煤矿",
                reason: "甲烷超标 2.6CH4 15分钟"
              }
            });
          }
          case index === 1: {
            return createWarn({
              info: {
                name: "海湾煤矿",
                reason: "甲烷超标 2.6CH4 15分钟"
              },
              par: ".map-top"
            });
          }
          case index === 2: {
            return createFirstWarn({
              level: 2,
              dur: 60
            });
          }
        }
      }
    };
  }
});
</script>

