<template>
  <div
    class="position-relative over-hidden"
    @mouseenter="removeTimer"
    @mouseleave="openTimer"
  >
    <div class="button">
      <img
        :src="
          require(`../../../${icon}${
            !isLast ? 'bottom-green' : 'top-gray'
          }.svg`)
        "
        :class="{ 'rotate-180': isLast }"
        @click="gotoPage"
        alt="下一页"
      />
      <img
        :src="
          require(`../../../${icon}${
            !isFirst ? 'bottom-green' : 'top-gray'
          }.svg`)
        "
        :class="{ 'rotate-180': !isFirst }"
        @click="downPage"
        alt="上一页"
      />
    </div>
    <transition-group name="next" tag="ul" class="mb0">
      <li
        v-for="(item, index) of newMessage[curIndex]"
        style="margin-top: 0.8rem;"
        :data-index="index"
        :key="item.id"
      >
        <slot :item="item" />
      </li>
    </transition-group>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onMounted, PropType, ref } from "vue";
export default defineComponent({
  name: "scroll-list",
  props: {
    data: {
      type: Array as PropType<IObj[]>,
      default: () => []
    },
    initialHeight: {
      type: String,
      default: ""
    },
    length: {
      type: Number,
      default: 5
    },
    dur: {
      type: Number,
      default: 5
    }
  },
  setup(props) {
    const curIndex = ref(0);
    const newMessage = ref<IObj[]>([[]]);
    const timerID = ref<number>();
    // 计算当前页和数据长度比较
    const isLast = computed(
      () => curIndex.value === newMessage.value.length - 1
    );
    const isFirst = computed(
      () => curIndex.value < 1 && curIndex.value < newMessage.value.length
    );

    function splitData() {
      // 将数据切分
      props.data.forEach((i: IObj) => {
        const lastArr = newMessage.value[newMessage.value.length - 1];
        if (lastArr.length < props.length) {
          lastArr.push(i);
        } else {
          newMessage.value.push([i]);
        }
      });
    }

    // 定时器装置
    const openTimer = () => {
      timerID.value = setInterval(() => {
        if (newMessage.value.length - 1 !== curIndex.value) {
          curIndex.value += 1;
        } else {
          curIndex.value = 0;
        }
      }, props.dur * 1000);
    };

    const removeTimer = () => {
      clearInterval(timerID.value);
    };

    onMounted(async () => {
      splitData();
      openTimer();
    });

    return {
      newMessage,
      curIndex,
      gotoPage() {
        !isLast.value && (curIndex.value += 1);
      },
      downPage() {
        !isFirst.value && (curIndex.value -= 1);
      },
      isLast,
      isFirst,
      icon: "assets/svg/dashboard/big-data/warn-message/",
      translate: `calc(-${100 * props.length}% - ${0.8 * props.length}rem)`,
      removeTimer,
      openTimer
    };
  }
});
</script>

<style lang="less" scoped>
.rotate-180 {
  transform: rotate(180deg);
}

.next-leave-active,
.next-enter-active,
.prev-leave-active,
.prev-enter-active {
  transition: all 2s linear;
}

.next-leave-to,
.next-enter-to {
  transform: translateY(v-bind(translate));
}

.prev-leave-to,
.prev-enter-to {
  transform: translateY(v-bind(translate));
}

.button {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 10;
  display: flex;
  width: 30px;
  height: 62px;
  padding: 8px;
  background: url("../../../assets/svg/dashboard/big-data/warn-message/box.svg");
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
</style>
