import { VNode } from "@vue/runtime-core";
import { createVNode, isVNode, render } from "vue";
import SecondWarn from "./second-warn.vue";

interface IOption {
  children?: VNode;
  dur?: number;
  par: string;
  info?: {
    name: string;
    reason: string;
  };
}

let vm: VNode;
let id = 1;

export function createWarn(opts: IOption) {
  const par = document.querySelector(opts.par);
  const container = document.createElement("div");
  container.className = `w100 h100 warn-${id++}`;
  if (par) {
    vm = createVNode(
      SecondWarn,
      {
        ...(opts as IObj),
        close: () => par.removeChild(container)
      },
      isVNode(opts.children)
        ? {
            default: () => opts.children
          }
        : null
    );
    render(vm, container);
    par.appendChild(container);
  }
}
