<template>
  <div ref="chartRef" />
</template>

<script lang="ts">
import { defineComponent, onMounted, PropType, Ref, ref } from "vue";
import useEcharts from "@/hooks/common/useEchart";

export default defineComponent({
  name: "riskCharts",
  props: {
    riskChartData: {
      type: Array as PropType<{ name: string; value: number }[]>,
      default: () => [
        {
          name: "瓦斯安全",
          value: 0
        },
        {
          name: "人员安全",
          value: 0
        },
        {
          name: "设备安全",
          value: 0
        },
        {
          name: "质安全",
          value: 0
        }
      ]
    }
  },
  setup(props) {
    const chartRef = ref<HTMLDivElement | null>(null);
    const { setOptions } = useEcharts(chartRef as Ref<HTMLDivElement>);
    onMounted(() => {
      setOptions({
        grid: {
          top: "10%",
          bottom: 0,
          left: 0,
          right: 0
        },
        xAxis: {
          max: 100,
          show: false
        },
        yAxis: [
          {
            data: props.riskChartData.map(i => i.name),
            inverse: true,
            show: false
          }
        ],
        series: [
          {
            //内
            type: "bar",
            barWidth: 7,
            legendHoverLink: false,
            symbolRepeat: true,
            silent: true,
            label: {
              show: true,
              color: "#fff",
              fontSize: "110%",
              position: [0, "-250%"],
              formatter: "{b}"
            },
            itemStyle: {
              color: (params: any) => {
                return params.dataIndex % 2 === 0
                  ? {
                      type: "linear",
                      x: 0,
                      y: 0,
                      x2: 1,
                      y2: 0,
                      colorStops: [
                        {
                          offset: 0,
                          color: "#12272A" // 0% 处的颜色
                        },
                        {
                          offset: 1,
                          color: "#06f398" // 100% 处的颜色
                        }
                      ]
                    }
                  : {
                      type: "linear",
                      x: 0,
                      y: 0,
                      x2: 1,
                      y2: 0,
                      colorStops: [
                        {
                          offset: 0,
                          color: "#12272A" // 0% 处的颜色
                        },
                        {
                          offset: 1,
                          color: "#EDB517" // 100% 处的颜色
                        }
                      ]
                    };
              }
            },
            data: props.riskChartData,
            z: 1,
            animationEasing: "elasticOut"
          },
          {
            type: "pictorialBar",
            animationDuration: 0,
            symbolRepeat: "fixed",
            symbolMargin: "20%",
            symbol: "roundRect",
            symbolSize: [4, 7],
            symbolBoundingData: 100,
            label: {
              show: true,
              color: "#fff",
              fontSize: "110%",
              position: ["89%", 0],
              formatter: "{c}"
            },
            itemStyle: {
              color: "#12272A"
            },
            data: props.riskChartData.map(i => i.value),
            z: 0,
            animationEasing: "elasticOut"
          },
          {
            //分隔
            type: "pictorialBar",
            itemStyle: {
              color: "#000"
            },
            symbolRepeat: "fixed",
            symbolMargin: 2,
            symbol: "roundRect",
            symbolClip: true,
            symbolSize: [1, 7],
            symbolPosition: "start",
            symbolOffset: [0, 0],
            symbolBoundingData: 100,
            data: props.riskChartData,
            z: 2,
            animationEasing: "elasticOut"
          }
        ]
      });
    });
    return {
      chartRef
    };
  }
});
</script>

<style scoped lang="less"></style>
