<template>
  <ul class="env-info flex-row just-end align-center">
    <li class="flex-column">
      <span class="env-info__title">TIME</span>
      <div>
        <span class="env-info__time">{{ time }}</span>
        <span class="env-info__date">{{ date }}</span>
      </div>
    </li>
    <li style="margin: 0 3rem;" class="flex-column">
      <span class="env-info__title">TEMP</span>
      <div class="flex-row align-center">
        <span>{{ temperature }}°C</span>
        <img
          class="ml10"
          src="../../../assets/svg/dashboard/big-data/map/weather.svg"
          alt=""
        />
      </div>
    </li>
    <li class="flex-column">
      <span class="env-info__title">PM2.5</span>
      <span>{{ pm }}</span>
    </li>
  </ul>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs } from "vue";
import moment from "moment";
export default defineComponent({
  name: "env-info",
  setup() {
    const data = reactive({
      date: "",
      time: "",
      temperature: "19",
      pm: "23"
    });
    setInterval(() => {
      data.time = moment().format("HH:mm:ss");
      data.date = moment().format("YYYY.M.D");
    });
    return {
      ...toRefs(data)
    };
  }
});
</script>

<style scoped lang="less">
.env-info {
  font-size: 3.2rem;
  color: #28daf6;

  &__title {
    font-size: 1.7rem;
    opacity: 0.5;
  }

  &__time {
    display: inline-block;
    margin-right: 0.2rem;
